import React, { Component } from "react";
import { Field, reduxForm } from "redux-form";
const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);

export const Card2Field = (props) => {
  return (
    <div className={props.size}>
      <div className="card">
        <div className="card-body">
          <h3>{props.title}</h3>
          <div className="row">
            <div className="col-md-6 col-lg-6">
              <Field
                name={"jumlah" + props.title}
                label="Jumlah"
                type="number"
                component={renderField}
              />
            </div>
            <div className="col-md-6 col-lg-6">
              <Field
                name={"netto" + props.title}
                label="Netto"
                type="number"
                component={renderField}
                readOnly={true}
              />
            </div>
            {props.title === "KU" ? (
              <>
                <div className="col-md-6 col-lg-6">
                  <label htmlFor="">A/C Tujuan</label>
                  <Field
                    name="supplier"
                    className="form-control"
                    component="select"
                  >
                    <option value="DEFAULT" key="DEFAULT">
                      silahkan pilih
                    </option>
                  </Field>
                </div>
                <div className="col-md-6 col-lg-6">
                  <Field
                    name="tanggalAC"
                    label="Tanggal A/C"
                    type="date"
                    component={renderField}
                  />
                </div>
              </>
            ) : props.title === "Giro" ? (
              <>
                <div className="col-md-6 col-lg-6">
                  <label htmlFor="">A/C Tujuan</label>
                  <Field
                    name="supplier"
                    className="form-control"
                    component="select"
                  >
                    <option value="DEFAULT" key="DEFAULT">
                      silahkan pilih
                    </option>
                  </Field>
                </div>
                <div className="col-md-6 col-lg-6">
                  <Field
                    name="tanggalAC"
                    label="Tanggal A/C"
                    type="date"
                    component={renderField}
                  />
                </div>
                <div className="col-md-6 col-lg-6">
                  <Field
                    name={"bank" + props.title}
                    label="Bank"
                    type="number"
                    component={renderField}
                  />
                </div>
                <div className="col-md-6 col-lg-6">
                  <Field
                    name={"no" + props.title}
                    label="No Giro"
                    type="number"
                    component={renderField}
                  />
                </div>
              </>
            ) : null}
          </div>
        </div>
      </div>
    </div>
  );
};
